.gallery {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-columns: repeat(var(--colGallery), 1fr);
    grid-gap: 1em;
    gap: 1em;
    margin: 5em 8em;
    justify-items: center;
    grid-auto-rows: 13em;
    /* background: var(--grey); */
}

.gallery > *:not(.lighthouse) {
    width: 100%;
    height: 100%;
    /* width: 20em;
    height: 13em; */
    cursor: pointer;
    position: relative;
    top: 0;
    transition: width .3s ease, heigth .3s ease, top .3s ease;
    object-fit: cover;
}

.gallery>*:not(.lighthouse):hover {
    /* width: calc(1.05 * 20em);
    height: calc(1.05 * 13em);
    top: calc(-0.025 * 20em);     */
    width: 105%;
    height: 105%;
    top: -2.5%;

}

.play-icon {
    height: 50%;
