    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-filter: brightness(2) drop-shadow(0 0 .5em rgba(0, 0, 0, 0.5));
