.lighthouse {
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(34, 34, 34, 0.98);
    width: 100vw;
    height: 100vh;
    padding: 2em;
    display: grid;
    grid-template-rows: calc(100% - 10em) 5em;
    grid-template-columns: 5em calc(100% - 16em) 5em;
    grid-template-areas: 
    "l-arrow current r-arrow"
    "thumbnails thumbnails thumbnails";
    grid-gap: 1em;
    gap: 1em;
    justify-items: center;
    -webkit-align-items: center;
            align-items: center;
    z-index: 90;
    -webkit-animation: .3s fadeout paused;
            animation: .3s fadeout paused;
}

.l-items {
    height: 100%;
    width: 100%;
    grid-area: thumbnails;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    position: relative;
    overflow-x: auto;
    overflow-y: hidden;
}

.l-items > * {
    height: 4.8em;
    width: 7em;
    cursor: pointer;
    object-fit: cover;
    margin-right: .5em;
    position: relative;
}

.currentThumbnail {
    border: solid rgb(255, 255, 255) .1em;
}

.currentPicture {
    grid-area: current;
    width: 100%;
    height: 100%;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.currentPicture > * {
    height: 100%;
    width: 100%;
    object-fit: contain;
}

.left-arrow {
    grid-area: l-arrow;
}

.right-arrow {
    grid-area: r-arrow;
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
}

.left-arrow, .right-arrow {
    width: 2em;
    -webkit-filter: brightness(.5);
            filter: brightness(.5);
    cursor: pointer;
    transition: -webkit-filter .3s ease;
    transition: filter .3s ease;
    transition: filter .3s ease, -webkit-filter .3s ease;
}

.left-arrow:hover, .right-arrow:hover {
    -webkit-filter: brightness(1);
            filter: brightness(1);
}

.close {
    width: 1.2em;
    height: auto;
    position: absolute;
    top: 3em;
    right: 8em;
    cursor: pointer;
    -webkit-filter: brightness(.5);
            filter: brightness(.5);
    transition: -webkit-filter .3s ease;
    transition: filter .3s ease;
    transition: filter .3s ease, -webkit-filter .3s ease;
    z-index: 91;
}

.close:hover {
    -webkit-filter: brightness(1);
            filter: brightness(1);
}

@-webkit-keyframes fadeout {
    from { opacity: 1;} to { opacity: 0; }
}

@keyframes fadeout {
    from { opacity: 1;} to { opacity: 0; }
}

@media only screen and (max-width: 768px) {
    .lighthouse {
        width: 100vw;
        height: 100vh;
        padding: 2em;
        grid-template-rows: calc(100% - 10em) 5em;
        grid-template-columns: 5em calc(100% - 16em) 5em;
        grid-template-areas: 
        "current current current"
        "thumbnails thumbnails thumbnails";
        grid-gap: 1em;
        gap: 1em;
    }
    .left-arrow, .right-arrow {
        display: none;
    }
    .close {
        top: 1em;
        right: 5em;
    }
}
